<template>
    <div class="flex-col">
        <!--查询条件-->

        <el-form :inline="true" class="query-box">
            <el-form-item label="注册日期">
                <el-date-picker
                        v-model="query.createDate"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        value-format="yyyy-MM-dd"
                        placeholder="选择日期">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="用户状态">
                <el-select placeholder="请选择" v-model="query.status">
                    <el-option
                            v-for="item in userStatusMap"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-input
                        placeholder="搜索姓名"
                        v-model="query.name"
                        @keyup.enter.native="search"
                        clearable>
                    <el-button slot="append" icon="el-icon-search" @click.native="search"></el-button>
                </el-input>
            </el-form-item>
        </el-form>
        <!--table列表-->
        <v-table v-on:pageChage="getData" :size="params.size" :page.sync="params.page"
                 :data="table.data" :total="table.total" :loading="table.loading">
            <el-table-column prop="name" label="姓名" align="center"></el-table-column>
            <el-table-column prop="mobile" label="手机号" align="center"></el-table-column>
            <el-table-column label="注册时间" align="center">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    {{ scope.row.createDate }}
                </template>
            </el-table-column>
            <el-table-column prop="status" label="状态" align="center">
                <template slot-scope="scope">
                    {{translator(scope.row.status,userStatusMap)}}
                </template>
            </el-table-column>
            <el-table-column prop="remarks" label="备注" align="center"></el-table-column>
            <el-table-column label="操作" align="center" width="390">
                <template slot-scope="scope">
                    <el-button
                            plain
                            type="info"
                            icon="el-icon-view"
                            @click="handleView(scope.row)">查看
                    </el-button>
                    <el-button
                            plain
                            type="primary"
                            icon="el-icon-edit"
                            @click="handleEdit(scope.row)">编辑
                    </el-button>
                    <el-button
                            plain
                            :type="!!scope.row.status ? 'warning' : 'success'"
                            :icon="!!scope.row.status ? 'el-icon-remove-outline' : 'el-icon-circle-check-outline'"
                            @click="handleStatus(scope.row)">
                        {{!!scope.row.status ? '禁用' : '启用'}}
                    </el-button>
                    <el-button
                            plain
                            type="danger"
                            icon="el-icon-refresh"
                            @click="handleResetPassword(scope.row)">重置密码
                    </el-button>
                </template>
            </el-table-column>
        </v-table>
        <!--dialog 弹层-->
        <el-dialog v-el-drag-dialog :title="dialogTitle" center :visible.sync="dialog"
                   width="460px" @close="closeDialogCallBack"
        >
            <el-form :model="formData" ref="form" status-icon :rules="rules" label-width="85px"
                     @keyup.enter.native="submitForm">

                <el-form-item label="角色:" prop="role">
                    <el-select v-model="formData.role" placeholder="请选择">
                        <el-option
                                v-for="item in roleList"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="姓名:" prop="name">
                    <el-input v-model="formData.name" placeholder="请输入姓名" auto-complete="off" :disabled="isView"></el-input>
                </el-form-item>

                <el-form-item label="手机:" prop="mobile">
                    <el-input v-model="formData.mobile" placeholder="请输入手机号" auto-complete="off" :disabled="isView"></el-input>
                </el-form-item>

                <el-form-item label="邮箱:" prop="email">
                    <el-input v-model="formData.email" placeholder="请输入手机号" auto-complete="off" :disabled="isView"></el-input>
                </el-form-item>

                <el-form-item label="备注:">
                    <el-input
                            v-model="formData.notes"
                            type="textarea"
                            :disabled="isView"
                            :autosize="{ minRows: 3, maxRows: 3}"
                            placeholder="备注">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button @click="dialog = false">取 消</el-button>
                <el-button type="primary" v-show="!isView" @click="submitForm">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script type="text/ecmascript-6">
    import vTable from '@/components/vTable'
    import {apiUserList} from "@/api";
    import {logic, translator,dataMapping} from "@/utils";
    import {$SYS_ERR, statusMap} from "@/config/dictionary";

    export default {
        name: 'managerList',
        data() {
            return {
                // 查询条件相关
                query: {
                    createDate: [],
                    status: '',
                    name: '',
                },
                // table列表相关
                table: {
                    data: [],
                    total: 0,
                    loading: false
                },
                params: {
                    size: 20,
                    page: 1
                },
                userStatusMap: statusMap,
                translator: translator,
                // 弹层相关
                dialogTitle: '',
                dialog: false,
                isView: false,
                formData:{
                    name:'',
                    mobile:'',
                    role:'',
                    email:'',
                    notes:''
                },
                rules: {
                    name: [
                        {required: true, message: '请输入姓名', trigger: 'blur'},
                    ],
                    role: [
                        {required: true, message: '请选择角色', trigger: 'change'}
                    ],
                    mobile: [
                        {required: true, message: '请输入手机号', trigger: 'blur'},
                        {validator: this.checkMobile, trigger: 'blur'}
                    ],
                    email: [
                        {type: 'email', message: '邮箱格式不正确', trigger: 'blur'},
                        {validator: this.checkEmail, trigger: 'blur'},
                    ]
                },
                rowData:null
            }
        },
        created() {
            this.getData();
        },
        computed:{
            roleList(){
                return this.$store.state.other.roleList;
            }
        },
        methods: {
            /**
             * 获取用户列表数据
             */
            getData(params = this.params) {
                this.table.loading = true;
                apiUserList(params)
                    .then(res => {
                        logic(res, result => {
                            this.table.data = result.data;
                            this.table.total = result.total;
                        });
                    })
                    .catch(err => {
                        this.$message({
                            type: 'error',
                            message: $SYS_ERR
                        });
                        throw Error(err);
                    })
                    .finally(() => {
                        this.table.loading = false;
                    })
            },
            /**
             *  筛选用户
             */
            search() {
                /**
                 *  这里分开写，防止点击分页时的请求参数受筛选条件的双向数据流的影响
                 *  唯有通过筛选条件进行的查询方可更新查询条件，点击翻页使用原始条件进行查询
                 */
                this.params.page = 1;
                this.getData(Object.assign(this.params, this.query));
            },
            /**
             * 查看用户详情
             */
            handleView(row) {
                this.dialogTitle = '查看';
                this.isView = true;
                this.rowData = row;
                this.openDialog();
            },
            /**
             * 编辑用户详情
             */
            handleEdit(row) {
                this.dialogTitle = '编辑';
                this.rowData = row;
                this.openDialog();
            },
            /**
             * 重置密码
             */
            handleResetPassword(row) {
                this.$confirm('确定要将该用户密码重置为六个0?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$notify({
                        title: '提示',
                        message: '密码重置成功',
                        type:'success',
                        duration: 800,
                        showClose: false
                    });
                });
            },
            /**
             * 切换用户状态
             */
            handleStatus(row) {
                let message = '';
                !!row.status ? message = '禁用' : message = '启用';
                this.$confirm('确定要'+message+'该用户？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    row.status = !row.status;
                    this.$notify({
                        title: '提示',
                        message: message+'成功',
                        type:'success',
                        duration: 800,
                        showClose: false
                    });
                });
            },
            /**
             * 打开弹层
             */
            openDialog() {
                this.dialog = true;
                dataMapping(this.formData,this.rowData);
            },
            /**
             * 关闭弹层回调
             */
            closeDialogCallBack() {
                this.$refs['form'].resetFields();
                setTimeout(()=>{
                    this.formData.notes = '';
                    this.isView = false;
                },200);
            },
            /**
             * 检查手机号是否合法
             */
            checkMobile() {

            },
            /**
             * 检查邮箱是否合法
             */
            checkEmail(){

            },
            // 提交表单
            submitForm() {

            }
        },
        components: {
            vTable
        }
    }
</script>

<style lang="scss" type="text/scss">
    .query-box {
        flex: 0 0 auto;
    }
</style>
